// Slider
.slick-slider {
  min-width: 0;
 }  //position: relative
// Bounding shell
.slick-list {
  overflow: hidden; }
// Tape slide
.slick-track {
  display: flex;
  align-items: flex-start; }
// Slide
.slick-slide {
  text-align: center;
  @include breakpoint(md) {
    margin: 0 15px; } }
// Active slide
.slick-slide.slick-active {}
// Main slide
.slick-slide.slick-current {}
// Centered slide
.slick-slide.slick-center {}
// Arrow
.slick-arrow {}
// Arrow prev
.slick-arrow.slick-prev {}
// Arrow next
.slick-arrow.slick-next {}
// Disabled arrow
.slick-arrow.slick-disabled {}
// Dots
.slick-dots {
  li {}
  button {} }


// Comments slider styles
.slider-comments {
  position: relative;
  .slick-arrow {
    position: absolute;
    top: 50%;
    z-index: 1;
    font-size: 0; }

  .slick-prev {
    top: unset;
    bottom: 0;
    left: 50%;
    width: 18px;
    height: 35px;
    background: url('../img/icons/icons.svg#slide-prev') no-repeat -8px;
    background-size: 35px 52px;
    transform: translateY(190%) translateX(-200%);
    @include breakpoint(md) {
      top: 50%;
      left: 10px;
      transform: none; } }

  .slick-next {
    top: unset;
    right: 50%;
    width: 18px;
    height: 35px;
    bottom: 0;
    background: url('../img/icons/icons.svg#slide-next') no-repeat -8px;
    background-size: 35px 52px;
    transform: translateY(190%) translateX(200%);
    @include breakpoint(md) {
      top: 50%;
      right: 10px;
      transform: none; } } }
//=========================================================

// Banners slider
.slider-banners {
  position: relative;
  .slick-slide {
    @include breakpoint(md) {
      margin: 0; } }

  img {
    width: 100%; }
  .slick-arrow {
    position: absolute;
    top: 50%;
    z-index: 1;
    font-size: 0;
    transform: translateY(-50%); }

  .slick-prev {
    bottom: 0;
    left: 10px;
    width: 18px;
    height: 35px;
    background: url('../img/icons/icons.svg#slide-prev') no-repeat -8px;
    background-size: 35px 52px; }

  .slick-next {
    right: 10px;
    width: 18px;
    height: 35px;
    bottom: 0;
    background: url('../img/icons/icons.svg#slide-next') no-repeat -8px;
    background-size: 35px 52px; } }
// end banners slider


// Works slider
.slider-works {
  position: relative;
  @include breakpoint(md) {
    margin: 0; }
  .slick-list {
    margin: 0 -15px; }
  img {
    min-width: 100%;
    max-height: 200px;
    object-fit: cover;
    height: 100%;
    @include breakpoint(sm) {
      max-height: 250px; } }

  &__item {
    overflow: hidden; }

  .slick-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    font-size: 0;
    width: 32px;
    height: 32px;
    background: none;
    z-index: 2;
    @include breakpoint(md) {
      top: -100px + 22.5px - 16px;
      transform: translateY(0);
      border: 2px solid #126EB9; } }

  .slick-prev {
    left: 0;
    background: url("../img/work-arrow-prev-light.svg") no-repeat center center;
    background-size: contain;
    @include breakpoint(md) {
      background: url("../img/work-arrow-prev-dark.svg") no-repeat center center;
      left: unset;
      right: 52px;
      &:active {
        background: url("../img/work-arrow-prev-light.svg") no-repeat center center #126EB9; } } }

  .slick-next {
    right: 0;
    background: url("../img/work-arrow-next-light.svg") no-repeat center center;
    background-size: contain;
    @include breakpoint(md) {
      background: url("../img/work-arrow-next-dark.svg") no-repeat center center;
      &:active {
        background: url("../img/work-arrow-next-light.svg") no-repeat center center #126EB9; } } } }

// end Works slider
