.works {
  margin: 0 0 55px 0;
  @include breakpoint(md) {
    margin: 0 0 70px 0; }
  @include breakpoint(lg) {
    margin: 0 0 100px 0; }

  &__container {
    overflow: visible; }

  &__slider {
    margin: 0 0 20px 0!important;
    @include breakpoint(md) {
      margin: 0 0 55px 0!important; } }

  &__link {
    text-align: center;
    @include breakpoint(md) {
      text-align: right; } } }



